@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital@1&display=swap);
html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  min-height: 100vh;
  margin: 0 60px;
  background: #f5f9e9;
  color: #373f51;
  font-size: 18px;
  font-weight: 400;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #373f51;
}

a:hover {
  color: #28965a;
}

.active-link {
  color: #28965a;
  font-weight: 700;
}

.calculator-page {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}

.calculator {
  width: 600px;
  background: #d3d3d3;
  color: #333;
  font-weight: 500;
  font-size: 1.2rem;
  text-align: center;
}

.calculator td {
  padding: 25px;
}

.calculator-heading {
  color: #d62828;
  font-style: italic;
}

.calculator .output {
  background: #6d6a75;
  color: #fff;
  text-align: right;
  font-size: 30px;
}

.calculator .orange-colour {
  background: #f28f3b;
}

input {
  background: none;
  border: none;
}

.home-page h2 {
  margin-top: 40px;
  color: #d62828;
  font-style: italic;
}

.welcome-msg {
  margin-top: 70px;
  line-height: 2.5rem;
  height: 100%;
}

.quotes-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.quotes-page span {
  color: #d62828;
  font-style: italic;
}

.navbar ul {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  width: 250px;
  font-size: 17px;
  font-weight: 400;
}

.nav-brand {
  font-size: 40px;
  color: #28965a;
  font-weight: 600;
}

